import React, { Component } from "react";
import CardList from "./component/card-list/CardList.component";
import SearchBox from "./component/search-box/SearchBox.component";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      monster: [],
      searchfield: "",
    };
  }
  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((users) => this.setState({ monster: users }));
  }
  handelChange = (e) => {
    this.setState({ searchfield: e.target.value });
  };
  render() {
    const { monster, searchfield } = this.state;
    const filterMonster = monster.filter((monster) => monster.name.toLowerCase().includes(searchfield.toLocaleLowerCase()));
    return (
      <div className="App">
        <h1>Monsters Rolodex</h1>
        <SearchBox placeholder="search monsters" handelChange={this.handelChange} />
        <CardList monster={filterMonster} />
      </div>
    );
  }
}

export default App;
